:root {
  --bg1: #eee;
  --border: #555;
  --lighter: #222;
  --input: #d0d0d0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir, segoe ui, helvetica neue, helvetica, Cantarell, Ubuntu, roboto, noto, arial, sans-serif;
}

main {
  margin: 26px;
  font-size: 14pt;
}

input {
  border: 1px solid var(--border);
  background: var(--input);
  max-width: 100%;
  border-radius: 0;
  padding: 4px;
  font-size: 1.4rem;
  font-weight: 600;
  display: block;
}

.container {
  background-color: var(--bg1);
  margin-top: 32px;
  padding: 24px;
}

h1 {
  text-align: center;
  font-size: 2rem;
}

h2 {
  color: var(--lighter);
  margin: 0 0 22px;
  font-size: 1.5rem;
  font-weight: bold;
}

h3 {
  font-size: 1.3rem;
}

label {
  color: var(--lighter);
  margin: 20px 0 0;
  font-size: 1rem;
  font-weight: bold;
  display: block;
}

.output-value {
  background-color: var(--input);
  word-break: break-all;
  padding: 4px;
  font-family: Menlo, Consolas, Monaco, Liberation Mono, Lucida Console, monospace;
  font-size: 1.2rem;
}

